/*RESERVE STORE*/
.reserve-store {
    background: {
        image: url("../images/reserve_store_bg.jpg");
        repeat: no-repeat;
        position: center;
        attachment: fixed;
        size: cover;
    };

    .inner {
        height: 600px;
        display: flex;
        justify-content: center;
        align-items: center;
    }

    .medal {
        width: 334px;
        height: 334px;
        perspective: 600px;

        .front, .back {
            width: 334px;
            height: 334px;

            position: absolute;
            backface-visibility: hidden;
            transition: 1s;
        }

        .front {
            transform: rotateY(0deg);
        }

        &:hover .front {
            transform: rotateY(180deg);
        }

        .back {
            transform: rotateY(-180deg); 

            .btn {
                position: absolute;
                    top: 230px;
                    left: 0;
                    right: 0;
                margin: auto;
            }   
        }

        &:hover .back {
            transform: rotateY(0deg);
        }
    }

}


