/* AWRADS */
.awards {
    background-color: #272727;

    .inner {
        padding: 40px 0;

        .swiper-prev, .swiper-next {
            width: 42px;
            height: 42px;
            border: 2px solid #ffffff;
            border-radius: 50%;
            outline: none;
            color: #ffffff;
            opacity: 0.3;
            display: flex;
            justify-content: center;
            align-items: center;
            position: absolute;
                top: 0;
                bottom: 0;
            margin: auto;
    
            &:hover {
                background-color: #ffffff;
                color: #333333;
            }
        }
    
        .swiper-prev {
            left: -100px;
        }
        .swiper-next {
            right: -100px;
        }
    }

    .swiper-container {
        width: 100%;
        height: 40px;
    }
    
}

#to-top {
    width: 42px;
    height: 42px;
    background-color: #333333;
    color: #ffffff;
    border: 2px solid #ffffff;
    border-radius: 10px;
    cursor: pointer;
    display: flex;
    justify-content: center;
    align-items: center;
    position: fixed;
        bottom: 30px;
        right: 30px;
    z-index: 9;
}




